<template>
   <div @mouseover="showTooltip($event)" @mouseleave="hideTooltip($event)">
      <img :src="getImgUrl()" />
   </div>
</template>

<script setup lang="ts">
import { defineProps } from 'vue';
import { BiomType } from './models/biom.type';
import Spot from './models/spot';
import { SpotType } from './models/spot.type';

const props = defineProps({ spot: {} as Spot, showTooltip: false });
let tooltipShown = false;
let tooltip: HTMLDivElement;

const getImgUrl = () => {
   const images = getFolder()!;
   return images('./' + props.spot.spotType + '.png');
};

const showTooltip = (event: MouseEvent) => {
   if (tooltipShown || props.spot.mismatch || !props.showTooltip) return;
   tooltip = document.createElement('div');
   tooltip.classList.add('tooltip');
   tooltip.innerText = getTooltip()!;
   document.body.appendChild(tooltip);
   const rect = (event.target as HTMLElement).getBoundingClientRect();
   tooltip.style.left = rect.x - 2 + 'px';
   tooltip.style.top = rect.y + rect.height + 'px';
   tooltip.style.width = rect.width + 'px';
   tooltipShown = true;
};

const hideTooltip = (event: MouseEvent) => {
   if (!tooltipShown) return;
   document.body.removeChild(tooltip);
   tooltipShown = false;
};

const getTooltip = () => {
   switch (props.spot.spotType) {
      case SpotType.BigTower:
         return 'Башня магов';
      case SpotType.Cave:
         return 'Шахта';
      case SpotType.City:
         return 'Город';
      case SpotType.DeadTrees:
         return 'Выжженая земля';
      case SpotType.Empty:
         return 'Степь';
      case SpotType.Farm:
         return 'Ферма';
      case SpotType.Forest:
         return 'Лес';
      case SpotType.Fortress:
         return 'Крепость';
      case SpotType.Hill:
         return 'Холмы';
      case SpotType.House:
         return 'Дома';
      case SpotType.Mountain:
         return 'Гора';
      case SpotType.Tower:
         return 'Башня';
      case SpotType.Tree:
         return 'Перелесок';
   }
};

const getFolder = () => {
   switch (props.spot.biomType) {
      case BiomType.None:
         return require.context('../assets/images/none', false, /\.png$/);
      case BiomType.Grass:
         return require.context('../assets/images/grass', false, /\.png$/);
      case BiomType.Gravel:
         return require.context('../assets/images/gravel', false, /\.png$/);
      case BiomType.Dirt:
         return require.context('../assets/images/dirt', false, /\.png$/);
      case BiomType.Sand:
         return require.context('../assets/images/sand', false, /\.png$/);
      case BiomType.Ice:
         return require.context('../assets/images/ice', false, /\.png$/);
   }
};
</script>
